import React from "react";
import "./button.scss";
import PropTypes from "prop-types";

//Lu : hm onClick se co 3 truong hop truyen gia tri
// TH1: ham da duoc dinh nghia o ngoai
      // + neu ham dinh ngha  cn c tham s th trong onClick = {(i s) => ham(i s)}
      // + neu ham inh ngha  khng cn tham s th trong onClick = {callback}
// TH2: ham cha c nh ngha  ngoi
      // + bc buc phi dng ()=>{ni dung cn thc thc hin}

const Button = (props) => {
  const {onClick}  = props;
  return (
    <button
      className={`btn ${props.className}`}
      onClick={onClick ? onClick : null}
    >
      {props.children}
    </button>
  );
};


export const OutlineButton = (props) => {
  const {onClick} = props
    return (
      <Button
        className={`btn-outline ${props.className}`}
        onClick={onClick ? onClick : null}
      >
        {props.children}
      </Button>
    );
  };


Button.propTypes = {
  onClick: PropTypes.func,
};

export default Button;
